			var res = {};
			//res = cmd("df / -h | awk '{print ($3)}' | tail -1") + "/ " + cmd("df / -h | awk '{print ($2)}' | tail -1") + " ("+cmd("df / | awk '{print ($5)}' | tail -1")+"used)";
			res.storageUsage = cmd("df / | awk '{print ($3)}' | tail -1")
